

// products style mob version


  
  .section-products{
    margin-bottom: 0;
  }
  
  .products__label {
    font-size: 18px;
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 1.67;
    text-align: center;
    color: var(--secondary-pink-text-color);

    margin-bottom: 15px;
  }

  .container-products {
    margin-top: 60px;
    text-align: center;
  }

  .products__list {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 148px;
  }

  .products__item {
    width: 280px;

    position: relative;
    padding-top: 160px;
    margin-bottom: 160px;
    padding-right: 32px;
    padding-left: 32px;
    

    border-radius: 24px;

    &:last-child {
      margin-bottom: 60px;
    }
  }

  .products__item:hover,
  .products__item:focus{
    transform: scale(1.1);
    box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.12), 1px 3px 3px rgba(0, 0, 0, 0.14), 1px 4px 3px rgba(0, 0, 0, 0.2);
    transition: transform var(--animation);
  }

  .container-products__title {
    font-size: 34px;
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 1.16;
    text-align: center;
    letter-spacing: 0.04em;
    color: var(--pink-text-color);

  }

  .container-products__title:hover,
  .container-products__title:focus{
    transform: rotate(-5deg);
    animation: glow 2s ease-in-out infinite alternate;
  }

  @keyframes glow{
    from{
        text-shadow: 0 0 50px #fff, 0 0 10px #fff, 0 0 20px #e60073,
        0 0 20px #e60073, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073;
    } to {
        text-shadow: 0 0 20px #fff, 0 0 30px #f880f2, 0 0 40px #f880f2, 0 0 50px #f880f2, 0 0 60px #f880f2,
        0 0 70px #f880f2, 0 0 80px #f880f2;
    }
  }

  @keyframes extend{
    0%{
        width: 40px;
        height: 40px;
        border-radius: 100px;
    } 10%{
        width: 40px;
        height: 40px;
        background: #ece8ef;
    } 20%{
        width: 40px;
        height: 40px;
        background: #4392f1;
    } 100%{
        width: 15px;
        height: 40px;
        border-radius: 100px;
        background: #4392f1;
    }
  }

  @-webkit-keyframes extend {
      0% {
          width: 40px;
          height: 40px;
          border-radius: 100px;
      }

      10% {
          width: 40px;
          height: 40px;
          background: #ece8ef;
      }

      20% {
          width: 40px;
          height: 40px;
          background: #4392f1;
      }

      100% {
          width: 15px;
          height: 40px;
          border-radius: 100px;
          background: #4392f1;
      }
  }

  @keyframes disappear{
    0%{
        opacity: 1;
    } 20% {
        color: #ece8ef;
    } 100% {
        opacity: 0;
    }
  }

   @-webkit-keyframes disappear {
       0% {
           opacity: 1;
       }

       20% {
           color: #ece8ef;
       }

       100% {
           opacity: 0;
       }
   }

   @keyframes appear{
    0%{
        opacity: 0;
    } 70%{
        opacity: 0;
    } 100%{
        opacity: 1;
    }
   }

   @-webkit-keyframes appear {
       0% {
           opacity: 0;
       }

       70% {
           opacity: 0;
       }

       100% {
           opacity: 1;
       }
   }


  .products-item__title {
    font-size: 20px;
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    color: var(--white-text-color);

    margin-bottom: 32px;
  }

  .products-item__description {
    font-size: 14px;
    font-family: 'DM Sans', sans-serif;
    font-weight: 700;
    line-height: 1.52;
    text-align: center;
    letter-spacing: 0.04em;
    color: var(--white-text-color);

    min-height: 107px;

    margin-bottom: 32px;
    margin-top: 32px;
  }

  .item__cream {
    background-color: rgba(255, 184, 202, 1);
  }

  .item__cream::before {
    height: 280px;
    width: 280px;
    margin-bottom: 20px;

    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    content: url(../images/mobile/mobile-product-1.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        content: url(../images/mobile/mobile-product-1@2x.png);
        transform: scaleX(50%) scaleY(50%);
        top: -45%;
        left: -25%;
      }

  
  }

  .item__coffee {
    background-color: var(--bg-color-third-pink);
  }

  .item__coffee::before {
    
    margin-bottom: 20px;

    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    content: url(../images/mobile/mobile-product-2.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        content: url(../images/mobile/mobile-product-2@2x.png);
        transform: scaleX(50%) scaleY(50%);
        top: -55%;
        left: -48%;
      }
  }

  .item__milkshakes {
    background-color: var(--bg-color-green);
  }

  .item__milkshakes::before {
    
    margin-bottom: 20px;

    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    content: url(../images/mobile/mobile-product-3.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        content: url(../images/mobile/mobile-product-3@2x.png);
        transform: scaleX(50%) scaleY(50%);
        top: -60%;
        left: -50%;
      }
  }

  .products-item__button {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    border: none;
    cursor: pointer;
    
    margin-bottom: 40px;
  }

  .products-item__button:hover,
  .products-item__button:focus{
    transform: rotate(-90deg) scale(1.5);
    border-radius: 10px;
    background-color: var(--bg-color-secondary-pink);
    box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.12), 1px 3px 3px rgba(0, 0, 0, 0.14), 1px 4px 3px rgba(0, 0, 0, 0.2);

    transition: transform var(--animation);
  }

  .products__item:hover .products-item__title{
    transform: rotate(-5deg) scale(1.2);
    transition: transform var(--animation);
  }

  .products__dots {
    position: relative;
  }

  .products__dots::before {
    position: absolute;
    content: ' ';
    top: 55%;
    left: 45%;
    transform: translate(-50%, -50%);

    background-color: var(--white-text-color);
    border-radius: 50%;
    width: 6px;
    height: 6px;
  }

  .products__dots::after {
    position: absolute;
    content: ' ';
    top: 55%;
    left: 55%;
    transform: translate(-50%, -50%);

    background-color: var(--white-text-color);
    border-radius: 50%;
    width: 6px;
    height: 6px;
  }

  .dots__icon {
    fill: var(--white-text-color);
  }

  .button-icon {
    fill: var(--pink-text-color);
  }


